body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.svg-inline--fa {
  font-size: 130px;
}

.shake {
  -webkit-animation: shake 0.15s;
          animation: shake 0.15s; 
  -webkit-animation-iteration-count: 2; 
          animation-iteration-count: 2; 
}

@-webkit-keyframes shake {
  0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
  50% { -webkit-transform: translate(4px, 0px); transform: translate(4px, 0px); }
  100% { -webkit-transform: translate(-4px, 0px); transform: translate(-4px, 0px); }
}

@keyframes shake {
  0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
  50% { -webkit-transform: translate(4px, 0px); transform: translate(4px, 0px); }
  100% { -webkit-transform: translate(-4px, 0px); transform: translate(-4px, 0px); }
}
ul.navList {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
}

.correct {
    -webkit-animation: growCorrect 0.5s;
            animation: growCorrect 0.5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.incorrect {
    -webkit-animation: growIncorrect 0.5s;
            animation: growIncorrect 0.5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes growCorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #004d40; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: white; }
}

@keyframes growCorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #004d40; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: white; }
}

@-webkit-keyframes growIncorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #b71c1c; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: white; }
}

@keyframes growIncorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #b71c1c; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: white; }
}
